}

.invalid-search input {
  background-color: rgb(236, 118, 118) !important;
  color: white !important;
  transition: 0.35s;
}
.error-search {
  transition: 0.3s;
  font-size: 13px;
  margin-bottom: 5px;
  width: 73%;
  border-radius: 10px;
