  color: rgb(144, 4, 4);
  background-color: rgb(250, 102, 102);
}
.invalid-search span {
  background-color: rgb(236, 118, 118) !important;
}

.category-item p {
  transition: 0.2s;
  margin: 0;
}
.category-item p:hover {
  color: black;
  transform: translateX(15px);
}

.category-item p::selection {
  color: red;
}

.nav-link {
  color: #050035 !important;
}
