.slider-content {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 103px;
  z-index: 999;
}
.slider-content-style-1 h1 {
  color: #050035;
  font-size: 100px;
  font-weight: bold;
  letter-spacing: 6px;
  margin-bottom: 4px;
  text-transform: uppercase;
}
